#homepage {
  background-color: #afceff;
  width: 100%;
  height: 100vh; }
  #homepage .wrapper {
    display: block;
    min-height: 80px;
    text-align: center; }
  #homepage .homepage-centered {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-transition: all 0.5s linear;
    -o-transition: all 0.5s linear;
    transition: all 0.5s linear; }
  #homepage.loading .homepage-centered {
    -webkit-transform: translate(-200%, -50%);
        -ms-transform: translate(-200%, -50%);
            transform: translate(-200%, -50%);
    opacity: 0;
    -webkit-transition: all 0.5s linear;
    -o-transition: all 0.5s linear;
    transition: all 0.5s linear; }
  #homepage #host-button {
    font-family: inherit;
    font-size: 3rem;
    background: none;
    -webkit-box-shadow: 1px 1px 3px 1px #555;
            box-shadow: 1px 1px 3px 1px #555;
    border: 3px solid black;
    width: 120px;
    height: 80px;
    line-height: 80px;
    border-radius: 10px;
    background-color: #639fff;
    cursor: pointer;
    margin: 0px auto;
    display: block;
    color: inherit;
    -webkit-text-stroke-width: 2px;
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear; }
    #homepage #host-button:hover {
      -webkit-transform: scale(1.1);
          -ms-transform: scale(1.1);
              transform: scale(1.1);
      background-color: #7cafff;
      -webkit-transition: all 0.2s linear;
      -o-transition: all 0.2s linear;
      transition: all 0.2s linear; }
    #homepage #host-button:active {
      background-color: #96beff; }
  #homepage h1 {
    font-size: 3rem; }



